export default {
	data() {
		return {
			userInfo:{},
			user:{
				pwd:'',
				confirmPwd:'',
				newPwd:''
			}
		}
	},
	methods: {
		goBack(){
			uni.navigateBack({
				delta:1
			})
		},
		getUserInfo(){
			uni.getStorage({
				key:'userInfo',
				success: (res) => {
					this.userInfo=res.data
				}
			})
		},
		forgetPwd(){
			uni.navigateTo({
				url:'../../../login-design/find-password/find-password'
			})
		}
	},
	onLoad(){
		this.getUserInfo()
	}
}
